#layout-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100%;
    
    overflow: hidden;
    padding: 0;
    margin: 0;

    position: relative;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}

svg {
    --svg-circle-size: 80;

    -webkit-box-sizing: border-box;

            box-sizing: border-box;
    position: absolute;
    top: -6rem;
    right: -7rem;
    z-index: 1;

    overflow: visible;

    width: -webkit-max-content;

    width: -moz-max-content;

    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;

    margin: 0;
    padding: 0;

}

svg circle {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    r: 150;
    cx: 50%;
    cy: 50%;

    margin: 0;
    padding: 0;

    stroke: black;
    stroke-width: 0px;
    fill: var(--accent-blue);
    opacity: 0.5;
}

.section-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100vh;

    padding: 0;
    margin: 0;

    border-style: none none solid none;
    border-width: 1px;
    border-color: white;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    background: var(--section-container-bg);
}

.section-container:nth-child(2n) {
    background: var(--section-container-alt-bg);
}

